export const updateMeta = state => {
  return {
    type: "UPDATE_META",
    payload: state
  };
};

export const uploadMeta = state => {
  return {
    type: "UPLOAD_META",
    payload: state
  };
};

export const updateAlert = state => {
  return {
    type: "UPDATE_ALERT",
    payload: state
  };
};

