export const dateAlertReducer = (state = initState.dateAlert, action) => {
  switch (action.type) {
    case "UPDATE_ALERT":
      const { alertVisibility, alertVariant, alertResult } = action.payload;

      return {
        ...state,
        alertVisibility,
        alertVariant,
        alertResult
      };

    default:
      break;
  }
  return state;
};

const metaReducer = (state = initState, action) => {
  switch (action.type) {
    case "UPDATE_META":
      let {
        fieldLabel,
        fieldName,
        fieldValue,
        isRequired,
        types
      } = action.payload;
      return {
        ...state,
        [fieldLabel]: {
          fieldLabel,
          fieldName,
          fieldValue,
          ...(isRequired && { isRequired }),
          ...(types && { types })
        }
      };
    case "UPLOAD_META":
      const payload = action.payload;
      return {
        ...state,
        [payload.fieldLabel]: {
          ...state[payload.fieldLabel],
          fieldValue: payload.fieldValue
        }
      };
    default:
      break;
  }
  return state;
};

export default metaReducer;
